.likeButton {
  height: 42px;
  padding: 0 40px;
  font-size: 18px;
  background-color: transparent;
  border-radius: 50px;
  margin-top: 2px;
  margin-bottom: 5px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 45%;
  font-size: 12px;
  margin: auto;
  margin-top: 30px;
  border-radius: 5px;
  padding-right: 15px;
  padding-left: 5px;
}

.card:hover {
  box-shadow: 0 8px 16px 0 gray;
}

.card > img {
  width: 100%;
  height: auto;
  padding-right: 5px;
  padding-left: 5px;
  /* border-radius: 5px; */
}

.container {
  padding: 2px 16px;
}

.copyrightText {
  font-size: 8px;
  /* font-weight: bold; */
  color: gray;
  padding: 5px;
}

.logoText {
  font-size: 23px;
  font-weight: 800;
  padding: 5px;
}
